.home {
  &.ui.container {
    margin: 0 !important;
  }
  h1.ui.header {
    &.popular-title, &.mood-title {
      font-size: 1.7em;
      color: var(--high-text-color);
    }
  }
  .ui.fluid.container.inner-wrapper {
    margin-left: 0 !important;
    margin-right: 0 !important;
    .ui.grid:first-child {
      margin: 0;
    }
    margin-top: 0.5em;
    .genres-card-pb-0 {
      .genres-grid {
        .column {
          padding-bottom: 0!important;
        }
      }
    }
  }

  .ui.grid > .row > .column {
    &.pl-wrapper {
      padding-left: 0;
      padding-right: 0;
    }
  }
}
