.wide-navigation {
  background-color: initial !important;
  padding: 0 1.5em 0 1.5em!important;
  .left.menu {
    .item {
      padding-left: 0;
      .input {
        input {
          &:focus {
            border-color: var(--text-color);
          }
          padding-left: 1.67143em !important;
          background: var(--background-color);
          color: var(--text-color);
        }
        i.icon {
          width: 17px;
          color: var(--text-color);
        }
      }
    }
  }
  .right.menu {
    width: 160px;
    .item {
      width: inherit;
      padding-right: 0 !important;
      .user-menu {
        width: 100% !important;
        .row:first-child {
          padding-right: 0;
        }
      }
      i.icon:nth-child(3) {
        padding-left: 1.5em;
      }
      i.icon {
        color: var(--text-color);
      }
    }
  }
}

.mobile-navigation {
  height: 50px;
  width: 100%;
  background-color: #1b3bad;
  .ui.grid {
    height: inherit;
    &:first-child {
      margin: 0 !important;
    }
    div.row {
      &:first-child {
        padding-top: 0.7rem;
        padding-bottom: 0.7rem;
      }
      .column {
        &:nth-child(3) {
          height: 100%;
          .mobile-right-wrapper {
            padding-top: 0 !important;
            padding-bottom: 0 !important;
            .mobile-user-menu-wraper {
              height: 100%;
              padding-right: 0 !important;
            }
          }
          padding-right: 0 !important;
          i.icon {
            &:nth-child(2) {
              margin-left: 15px !important;
            }
          }
        }
      }
    }
  }
}

.wide-navigation,
.mobile-navigation {
  .user-menu {
    &.grid {
      max-width: inherit;
      .row {
        &:first-child {
          padding-right: 1em;
          justify-content: flex-end;
        }
        padding-top: 0 !important;
        padding-bottom: 0 !important;
        .column {
          &.user-avatar {
            padding-left: 0.2em !important;
            padding-right: 0.2em !important;
            width: auto !important;
            a {
              display: block;
              width: 30px;
              height: 30px;
              line-height: 30px;
              background-color: #21ba45;
              border-radius: calc(100% / 2);
              text-align: center;
              font-weight: bold;
              margin: auto;
              color: black;
            }
          }
          &.user-info {
            padding-left: 0.5em !important;
            padding-right: 0 !important;
            font-weight: 600;
            font-size: 0.9em;
          }
        }
      }
    }
    .ui.dropdown {
      position: absolute;
      margin-top: -1em;
      margin-left: -1em;
      .menu {
        background-color: var(--secondary-background-color);
      }
      .dropdown.icon {
        padding: 1em;
        margin-top: -1em;
        margin-left: -0.2rem;
        opacity: 0;
      }
      .item .text {
        font-size: 1rem !important;
      }
    }
  }
}
