.pageHeader {
  height: 147px;
  background: rgba(70, 92, 102, 0.65);
  position: relative;
  z-index: 1;
  -webkit-box-shadow: 0 8px 4px -4px #1a1c23;
  -moz-box-shadow: 0 8px 4px -4px #1a1c23;
  box-shadow: 0 8px 4px -4px #1a1c23;
  .grid {
    height: 100%;
    &.series-wide {
      .row .column {
        &:nth-child(1) {
          width: 170px !important;
        }
        &:nth-child(2) {
          padding-left: 0 !important;
          h1 {
            font-size: 3em;
          }
        }
      }
    }
    .row {
      &:nth-child(2) {
        padding-top: 0 !important;
      }
      img.tiny.widescreen {
        width: 115px;
      }
    }
  }
}
