@mixin reset-list {
  background-color: var(--primary-color);
}

@mixin menu-item-before {
  width: 205px;
  height: $menu-item-height;
  z-index: -1;
  border-radius: 4px;
  margin-left: 20px;
}

[data-theme="dark"] {
  background-color: var(--primary-color);
}

@media (prefers-color-scheme: dark) {
  .ui.segment {
    background-color: var(--background-color);
  }
}

$menu-item-height: 42px;
$menu-item-left-space: 30px;
$sidebar-width: 250px;

.app-sidebar {
  @include lg {
    height: 100vh;
  }
  &.pushable {
    overflow-x: initial;
  }
  &.ui.segment {
    border: initial;
    .pusher {
      height: auto;
      overflow-y: auto;
      .ui.segment {
        // padding: 0em 1em;
        // padding-top: 1em;
        padding: 0!important;
      }
      @include lg {
        width: calc(100% - #{$sidebar-width});
        .ui.segment {
          padding-left: 3em;
        }
      }
    }
  }
  .ui.wide.left.sidebar,
  .ui.wide.right.sidebar {
    width: $sidebar-width;
  }
  .ui.visible.wide.left.sidebar ~ .fixed,
  .ui.visible.wide.left.sidebar ~ .pusher {
    transform: translate3d($sidebar-width, 0, 0);
  }
  .ui.vertical.menu {
    background-color: var(--background-color);
    box-shadow: 1px 0 0 0 #414247;
  }
  .ui.vertical.sidebar.menu > div.header {
    img {
      margin-left: $menu-item-left-space - 6;
      width: 135px;
    }
    height: 80px;
    display: flex;
    align-items: center;
  }
  .ui.icon.menu .item {
    &:before {
      height: 0px;
    }
    text-align: initial;
    padding: 0;
    padding-left: $menu-item-left-space;
    height: $menu-item-height;
    line-height: $menu-item-height;
    color: var(--text-color);
  }
  .ui.menu {
    border: initial;
  }
  .ui.menu span.item {
    color: var(--secondary-text-color) !important;
    line-height: 30px !important;
    height: 30px !important;
    padding-left: $menu-item-left-space !important;
  }
  .ui.menu a.item {
    &.active {
      color: var(--link-active-color);
      &::before {
        background-color: var(--menu-active-item-bg-color);
        @include menu-item-before;
      }
    }
    &:hover {
      &.active {
        color: var(--link-active-color);
      }
    }
    &:hover:not(.active) {
      background: none;
      color: var(--text-color);
      &::before {
        background-color: var(--hover-color);
        @include bg-transition;
        @include menu-item-before;
      }
    }
  }
  .ui.labeled.icon.menu .item > .icon {
    float: left;
    width: 40px;
    line-height: $menu-item-height;
    font-size: 1.11428571em !important;
  }
}
