.showcase {
  .ui.header {
    color: var(--high-text-color);
    font-size: 1.7rem;
    margin-bottom: 0.5;
  }
  .ui.cards {
    height: 240px;
    position: relative;
    margin: 0;
    overflow-x: auto;
    white-space: nowrap;
    overflow-y: hidden;
    display: block;
    &::-webkit-scrollbar {
      display: none;
    }
  }
  .ui.card,
  .ui.cards > .card {
    &:hover {
      box-shadow: initial;
    }
    margin-left: 0;
    margin-right: 1.5em;
    position: relative;
    background-color: initial !important;
    box-shadow: initial;
    width: 160px;
    display: inline-block !important;
    .content:first-child {
      background-repeat: no-repeat;
      background-size: cover;
      border-radius: 20px !important;
      height: 160px;
      box-shadow: 0px 9px 16px rgba(119, 25, 25, 0.25);
      display: flex;
      justify-content: center;
      background-position: center;
    }
    .extra.content {
      padding-left: 0.5em;
      padding-right: 0.5em;
      max-height: 56px;
      .header {
        color: var(--high-text-color);
        font-size: 1.1em;
      }
      .meta {
        opacity: var(--text-medium);
        color: var(--text-color);
        font-size: 0.9em;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        padding-top: 3px;
      }
    }
  }
}
