.ui.checkbox {
  label {
    &:before {
      background-color: var(--background-color)!important;
      border-color: var(--secondary-background-color)!important;
    }
    &:after {
      color: var(--text-color)!important;
    }
    color: var(--text-color)!important;
    &:hover, &:focus {
      color: var(--text-color)!important;
    }
  }
}
