.playlist {
  &.ui.container {
    margin-left: 0 !important;
    margin-right: 0 !important;
  }
  .playlist-item {
    @include bg-transition;
    background-color: var(--secondary-background-color);
    border-radius: 5px;
    margin-bottom: 8px;
    height: 61px;
    &:hover {
      background-color: var(--secondary-hover-color);
    }
    &.row {
      padding-top: 0.5em !important;
      padding-bottom: 0.5em !important;
    }
    .column {
      padding-left: 0.5em !important;
      padding-right: 0.5em !important;
      line-height: 1.2285em;
      &.play {
        &.fix {
          width: 45px !important;
        }
      }
      &.image {
        &.fix {
          width: 55px !important;
        }
        .image-wrapper {
          overflow: hidden;
          border-radius: 5px;
        }
      }
      &.actions {
        &.fix {
          width: 85px !important;
          .actions-wrapper {
            justify-content: flex-end;
          }
        }
      }
    }
    .play-wrapper {
      text-align: center;
    }
    .description-wrapper {
      .pl-desc-series {
        font-weight: bold;
      }
      .pl-desc {
        font-size: 0.8em;
      }
    }
    .actions-wrapper {
      display: flex;
      justify-content: center;
      font-size: 1.4em;
      height: 27px;
      .ui.dropdown {
        .dropdown.icon {
          margin: 0;
          &:before {
            content: '';
          }
        }
        .menu {
          background-color: var(--background-color);
          .item {
            &:hover {
              background-color: var(--secondary-hover-color);
            }
            @include bg-transition;
            color: var(--text-color);
          }
        }
      }
    }
    .date-wrapper {
      font-size: 0.9em;
      text-align: center;
    }
    .genres-wrapper {
      font-size: 0.9em;
    }
    .duration-wrapper {
      font-size: 0.9em;
    }
  }
}
