.toast-container {
  z-index: 100;
  position: absolute; 
  right: 0;
  .toast > .ui.card {
    // box-shadow: 3px 4px 9px 0px var(--shadow-color), 0 2px 8px 1px var(--shadow-color);
    box-shadow: 1px 0px 7px 0px var(--shadow-color);
    &.success {
      box-shadow: 1px 0px 7px 0px var(--success-color);
    }
    &.error {
      box-shadow: 1px 0px 7px 0px var(--danger-color);
    }
  }
}
