.account {
  &.container {
    margin: 0!important;
  }
  .cover-area {
    margin: 0!important;
    padding: 1.5em 0;
    line-height: 100;
    background-color: #4a2e80;
    img {
      &.small {
        width: 100px!important;
        border: solid 2px;
      }
      margin: auto;
    }
  }
  .tab-container {
    margin-top: 1em;
    .ui.secondary.pointing.menu .item {
      &.active {
        background-color: var(--secondary-background-color);
        color: var(--text-color);
      }
    }
    .ui.secondary.pointing.menu {
      &::-webkit-scrollbar {
        display: none;
      }
      overflow-x: auto;
      white-space: nowrap;
      overflow-y: hidden;
      border-bottom: 2px solid rgba(34, 36, 38, 0.88);
      a:not(.active) {
        opacity: var(--text-disabled)!important;
      }
    }
  }
  .profile, .change-email, .change-password {
    &.grid:first-child {
      margin-top: 0!important;
    }
  }
}
