:root {
  --primary-color: #f0f3fb;
  --secondary-color: #e7eaf2;
  --background-color: #f6f9ff;
  --text-color: #3a3c43;
  --bg-transtion-speed: 350ms;
}

@mixin dark {
  --primary-color: #595b62;
  --secondary-color: #6c6f76;
  --background-color: #1a1c22;
  --secondary-background-color: #2b2d35;
  --high-text-color: white;
  --text-color: #a5aaba;
  --secondary-text-color: #727278;
  --text-medium: .6;
  --text-disabled: .33;
  --hover-color: #2b2d35;
  --shadow-color: #0e1017;
  --secondary-hover-color: #383b44;
  --link-active-color: white;
  --menu-active-item-bg-color: #3D50FA;
  --scrollbar-thumb-color: #343740;
  --danger-color: #d64848;
  --success-color: #048004;
}

// Small tablets and large smartphones (landscape view)
$screen-sm-min: 576px;

// Small tablets (portrait view)
$screen-md-min: 768px;

// Tablets and small desktops
$screen-lg-min: 992px;

// Large tablets and desktops
$screen-xl-min: 1200px;

[data-theme="dark"] {
  @include dark;
}

@media (prefers-color-scheme: dark) {
  :root {
    @include dark;
  }
}

// Small devices
@mixin sm {
  @media (min-width: #{$screen-sm-min}) {
      @content;
  }
}

// Medium devices
@mixin md {
  @media (min-width: #{$screen-md-min}) {
      @content;
  }
}

// Large devices
@mixin lg {
  @media (min-width: #{$screen-lg-min}) {
      @content;
  }
}

// Extra large devices
@mixin xl {
  @media (min-width: #{$screen-xl-min}) {
      @content;
  }
}

@mixin bg-transition {
  -webkit-transition: background var(--bg-transtion-speed);
  transition: background var(--bg-transtion-speed);
}

body {
  background-color: var(--background-color);
  color: var(--text-color);
  letter-spacing: .3px;
  @include lg {
    overflow: hidden;
    overscroll-behavior-y: none;
  }
  
}

a {
  &:hover {
    color: var(--secondary-text-color);
  }
  font-size: 1em;
  color: var(--text-color);
}

::placeholder {
  color: var(--primary-color)!important;
}
