.login-modal {
  &.ui.modal {
    .close {
      color: var(--text-color);
    }
    .header:not(.ui) {
      text-align: center;
      padding-top: 2.5rem!important;
    }
    .content {
      background-color: var(--background-color)!important;
      padding: 2.5rem!important;
      padding-bottom: 1rem!important;
      padding-top: 1rem!important;
      .ui.form {
        .empty {
          label {
            opacity: 0;
          }
        }
      }
    }
    .actions {
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding: 1rem 2.3rem!important;
      padding-bottom: 2.5em!important;
      button {
        &:hover {
          opacity: .9!important;
        }
        &:active {
          opacity: .8!important;
        }
        width: 100%;
        margin: 0!important;
        background-color: var(--menu-active-item-bg-color)!important;
      }
      .login-actions-item {
        padding-top: 1.3rem;
        font-size: .8rem;
        a {
          font-weight: bold;
          padding-left: .8rem;
          text-decoration: underline;
        }
      }
    }
  }
}
