.mainPlayer {
  &.container {
    width: 100vw !important;
  }
  @media (max-width: 767px) {
    &.container {
      height: 70px;
    }
  }
  position: fixed;
  height: 80px;
  background-color: #0f0f13;
  bottom: 0;
  box-shadow: 0px 1px 6px 0px black;

  .player-controls {
    .row {
      .column {
        display: flex;
        justify-content: space-evenly;
      }
    }
    .volume-container {
      width: 20px;
      position: absolute;
    }
    .volume-slider {
      position: absolute;
      padding-left: 3px;
      padding-right: 4px;
      top: -120px;
      .volume-bg-layer {
        position: absolute;
        background-color: var(--secondary-background-color);
        padding: 3px 6px 5px 5px;
        &:before {
          content: '';
          width: 3.5px;
          height: 83%;
          left: 9.5px;
          background-color: var(--menu-active-item-bg-color);
          display: block;
          position: absolute;
          border-radius: 2px;
          margin-top: 6px;
        }
      }
      .rs-container {
        border: initial;
      }
    }
  }
  .info {
    .column {
      &.info-image {
        max-width: 70px;
      }
      padding: 0 !important;
    }
    .presenter {
      color: var(--secondary-text-color);
    }
  }
  .timeline {
    width: auto;
    .rs-container {
      &:before {
        content: '';
        width: 100%;
        height: 3.5px;
        top: 5.5px;
        background-color: var(--menu-active-item-bg-color);
        display: block;
        position: absolute;
        border-radius: 2px;
      }
      -webkit-box-shadow: 0px 0px 31px -6px rgba(61, 80, 250, 1);
      -moz-box-shadow: 0px 0px 31px -6px rgba(61, 80, 250, 1);
      box-shadow: 0px 0px 31px -6px rgba(61, 80, 250, 1);
      background-color: #181a2b;
      border: initial;
    }
    .column.row {
      .column.duration {
        width: 50px !important;
        font-size: 0.9em;
      }
    }
  }
  .actions {
    .player-actions {
      width: 100%;
      text-align: right;
    }
  }
  .small-mobile {
    .player-controls {
      .icon {
        font-size: 1.1em;
        margin: 0 0.3em 0 0.3em;
      }
    }
    &.info {
      padding-left: 0 !important;
      padding-right: 0 !important;
      font-size: 0.9em;
      .player-info {
        .row {
          .info-image {
            width: 40px !important;
            margin-right: 10px;
          }
        }
      }
    }
  }
  .mobile {
    .player-controls {
      .icon {
        font-size: 1.3em;
        margin: 0 0.3em 0 0.3em;
      }
    }
    &.info {
      padding-left: 0 !important;
      padding-right: 0 !important;
      font-size: 0.9em;
      .player-info {
        .row {
          .info-image {
            width: 40px !important;
            margin-right: 10px;
          }
        }
      }
    }
  }
  .mobile-plus {
    .player-controls {
      .icon {
        font-size: 1.3em;
        margin: 0 0.3em 0 0.3em;
      }
    }
    &.info {
      padding-left: 0 !important;
      font-size: 0.9em;
      .player-info {
        .row {
          .info-image {
            width: 45px !important;
            margin-right: 10px;
          }
        }
      }
    }
  }
  .tablet {
    .player-controls {
      .icon {
        font-size: 1.5em;
        margin: 0 0.5em 0 0.5em;
      }
    }
    &.info {
      padding-left: 0 !important;
    }
  }
  .computer {
    .player-controls {
      .icon {
        font-size: 1.4em;
        margin: 0 0.4em 0 0.4em;
        &:last-child {
          margin-left: 1.2em !important;
        }
      }
    }
    &.info {
      padding-left: 0 !important;
    }
  }
  .widescreen {
    .player-controls {
      .icon {
        font-size: 1.6em;
        margin: 0 0.5em 0 0.5em;
        &:last-child {
          margin-left: 1.5em !important;
        }
      }
    }
  }
  .row.mini-timeline {
    position: absolute;
    padding-top: 0;
    padding-bottom: 0;
    .column:first-child {
      padding-left: 0;
      padding-right: 0;
    }
    .mini-content {
      width: 100%;
      height: 3.5px;
      background-color: var(--secondary-background-color);
      div {
        width: 50%;
        height: 100%;
        background-color: var(--menu-active-item-bg-color);
      }
    }
  }
}
