.genres-grid {
  .row {
    .column {
      margin-bottom: .5em!important;
      padding-left: 0!important;
      padding-right: .5em!important;
    }
  }
  .ui.card {
    &:hover {
      box-shadow: initial;
      background-color: initial;
    }
    width: 100%;
    height: 100px;
    background-color: var(--secondary-background-color);
    box-shadow: initial;
    .content {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: .5em .5em;
      background-color: var(--secondary-background-color);
      .header {
        color: var(--text-color);
      }
    }
  }
}
